import {Injectable} from '@angular/core';
import {ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot} from '@angular/router';
import {VrWebSocketService} from '../../modules/start-vr/services/socket/vr-web-socket.service';

@Injectable({
  providedIn: 'root'
})
export class AuthGuardService implements CanActivate {

  constructor(private router: Router, private vrWebSocketService: VrWebSocketService) {
  }


  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): boolean {
    if (state.url.includes('login')) {
      this.vrWebSocketService.deactivateWebSocketConnection();
      return true;
    }

    if (state.url.includes('logout')) {
      this.vrWebSocketService.deactivateWebSocketConnection();
      return true;
    }

    this.vrWebSocketService.initializeWebSocketConnection(); // nota, se già creata non la ricreo
    return true;
  }
}
