import {Injectable} from '@angular/core';
import {BehaviorSubject} from 'rxjs';
import {CALLSTATUS} from '../enums/callstatus.enum';
import {IStatusDetail} from '../interfaces/iStatusDetail';
import {AudioService} from './audio.service';

@Injectable({
  providedIn: 'root'
})
export class StatusDetailService {

  private statusDetailBS = new BehaviorSubject(null);

  constructor(private audioService: AudioService) {
  }

  setStatus(callStatus: CALLSTATUS, senderId?: string, receiverId?: string) {
    let next: IStatusDetail;
    switch (callStatus) {
      case CALLSTATUS.ERROR: {
        this.audioService.stopSound();
        next = {
          code: callStatus,
          title: {
            txt: 'Si è verificato un errore'
          },
          subtitle: {
            txt: 'Smart VR'
          },
          message: {
            txt: 'Ricaricare la pagina. Se il problema persiste contattare un amministratore'
          },
          button: {
            srcImg: '../../../../../assets/img/error.png',
            class: 'pulse-inverted'
          }
        };
        break;
      }
      case CALLSTATUS.SESSION_PENDING: {
        this.audioService.stopSound();
        next = {
          code: callStatus,
          title: {
            txt: 'Inizializzazione'
          },
          subtitle: {
            txt: 'Smart VR'
          },
          message: {
            txt: 'In attesa di una sessione..'
          },
          button: {
            srcImg: '../../../../../assets/img/logo.jpg',
            class: 'no-pointer'
          }
        };
        break;
      }
      case CALLSTATUS.NOT_REGISTRED: {
        next = {
          code: callStatus,
          title: {
            txt: 'Inizializzazione'
          },
          subtitle: {
            txt: 'Smart VR'
          },
          message: {
            txt: 'Registrazione in corso..'
          },
          button: {
            srcImg: '../../../../../assets/img/logo.jpg',
            class: 'no-pointer'
          }
        };
        break;
      }
      case CALLSTATUS.FREE: {
        this.audioService.playSound('../../../../assets/wav/signin.wav');
        next = {
          code: callStatus,
          title: {
            txt: 'Benvenuto, ' + senderId
          },
          subtitle: {
            txt: 'Smart VR'
          },
          message: {
            txt: 'Sei online!'
          },
          button: {
            srcImg: '../../../../../assets/img/logo.jpg',
            class: 'no-pointer'
          }
        };
        break;
      }
      case CALLSTATUS.BUSY: {
        next = {
          code: callStatus,
          class: 'dark',
          title: {
            txt: 'Chiamata terminata'
          },
          subtitle: {
            txt: 'Smart VR'
          },
          message: {
            txt: 'Linea Occupata'
          },
          button: {
            srcImg: '../../../../../assets/img/hangup.jpg',
          }
        };
        break;
      }
      case CALLSTATUS.RINGING: {
        this.audioService.playSound('../../../../assets/wav/ring.wav');
        next = {
          code: callStatus,
          class: 'dark-pulse',
          title: {
            txt: 'Chiamata da ' + receiverId
          },
          subtitle: {
            txt: 'Smart VR'
          },
          message: {
            txt: 'Clicca per rispondere..'
          },
          button: {
            srcImg: '../../../../../assets/img/ringing.gif',
            class: 'pulse'
          },
          button_secondary: {
            srcImg: '../../../../../assets/img/hangup.gif',
            class: 'pulse'
          }
        };
        break;
      }
      case CALLSTATUS.CALLING: {
        this.audioService.playSound('../../../../assets/wav/tone.wav', true);
        next = {
          code: callStatus,
          class: 'dark-pulse',
          title: {
            txt: 'Chiamata in corso..'
          },
          subtitle: {
            txt: 'Smart VR'
          },
          message: {
            txt: 'Clicca per riagganciare..'
          },
          button: {
            srcImg: '../../../../../assets/img/hangup.jpg',
            class: 'pulse'
          }
        };
        break;
      }
      case CALLSTATUS.CALL: {
        this.audioService.playSound('../../../../assets/wav/dialup.wav');
        next = {
          code: callStatus,
        };
        break;
      }
      case CALLSTATUS.HANGUP: {
        this.audioService.playSound('../../../../assets/wav/dialup.wav');
        next = {
          code: callStatus,
          class: 'dark',
          title: {
            txt: 'Chiamata Terminata'
          },
          subtitle: {
            txt: 'Smart VR'
          },
          message: {
            txt: ''
          },
          button: {
            srcImg: '../../../../../assets/img/hangup.jpg',
            class: 'no-pointer'
          }
        };
        break;
      }
      case CALLSTATUS.HANGUP: {
        next = {
          code: callStatus,
          class: 'dark',
          title: {
            txt: 'Chiamata Terminata'
          },
          subtitle: {
            txt: 'Smart VR'
          },
          message: {
            txt: ''
          },
          button: {
            srcImg: '../../../../../assets/img/hangup.jpg',
            class: 'no-pointer'
          }
        };
        break;
      }
      default: {
        break;
      }
    }
    this.statusDetailBS.next(next);
  }

  getStatus(){
    return this.statusDetailBS.getValue();
  }

  getStatusDetailBS() {
    return this.statusDetailBS;
  }
}
