import {Injectable} from '@angular/core';
import {BehaviorSubject} from 'rxjs';
import {IClient} from '../interfaces/iClient';
import {CallCenterService} from './http/call-center.service';

@Injectable({
  providedIn: 'root'
})
export class CallcenterClientStatusService {

  private callcenterClientsStatusMap: Map<string, IClient> = new Map();

  private callcenterClientsStatusBS = new BehaviorSubject(null);

  constructor(private callCenterService: CallCenterService) {
  }

  getCallcenterClientsStatusBS() {
    return this.callcenterClientsStatusBS;
  }

  refreshCallcenterClientsStatus() {
    this.callCenterService.getAllClients().subscribe((response) => {
      this.setCallCenterClientsStatus(response as IClient[]);
    });
  }

  setCallCenterClientStatus(client: IClient) {
    this.callcenterClientsStatusMap.set(client.username, client);
    this.callcenterClientsStatusBS.next(this.callcenterClientsStatusMap);
  }

  setCallCenterClientsStatus(clients: IClient[]) {
    clients.forEach((client) => {
      this.setCallCenterClientStatus(client);
    });
  }
}
