import {Injectable} from '@angular/core';
import {LogService} from './log.service';

@Injectable({
  providedIn: 'root'
})
export class AudioService {

  private audio: HTMLAudioElement;

  constructor(private logService: LogService) {
  }

  stopSound() {
    if (this.audio) {
      if (this.audio.pause) {
        this.audio.pause();
      }
      if (this.audio.remove) {
        this.audio.remove();
      }
    }
  }

  playSound(src: string, loop?: boolean) {
    this.stopSound();
    this.audio = new Audio();
    this.audio.src = src;
    this.audio.load();
    this.audio.loop = loop || false;
    const playedPromise = this.audio.play();
    if (playedPromise) {
      playedPromise
        .then(() => {

        })
        .catch((e) => {
/*          this.logService.appendLog('ERROR', 'errore riproduzione suono', e);*/
          if (e.name === 'NotAllowedError' ||
            e.name === 'NotSupportedError') {
/*            this.logService.appendLog('ERROR', 'errore riproduzione suono', e);*/
          }
        });
    }
  }
}
