import {Inject, Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})

export class HttpCommonService {

  protected SKIP_ERROR_HEADER = {'X-JDRO-Skip-Errors': 'true'};

  //private baseHref = '/start-vr';

  constructor(
    @Inject('BASE_HREF') private baseHref: string,
    @Inject('GATEWAY_HREF') private gatewayHref: string,
    protected http: HttpClient
  ) {
  }

  public getUrl(): string {
    return this.baseHref;
  }

  public setUrl(value: string) {
    this.baseHref = value;
  }

  public getServerUrl(path: string): string {
    return (this.baseHref + path);
  }

  public getGatewayUrl(path: string): string {
    return (this.gatewayHref + path);
  }

  public getOptions(skipErrorInterceptor?: boolean) {
    return skipErrorInterceptor && skipErrorInterceptor === true ? {headers: this.SKIP_ERROR_HEADER} : {};
  }

}
