import {Routes} from '@angular/router';
import {AuthGuardService} from './shared/services/auth-guard.service';

export const APP_ROUTES: Routes = [
  /*  {
      path: 'account',
      loadChildren: './modules/account/account.module#AccountModule'
    },*/
  {
    path: 'home',
    redirectTo: 'vr/home',
    pathMatch: 'full',
    canActivate: [AuthGuardService]
  },
  {
    path: 'index',
    redirectTo: 'vr/home',
    pathMatch: 'full',
    canActivate: [AuthGuardService]
  },
  {
    path: 'login',
    redirectTo: 'admin/account/login',
    pathMatch: 'full',
    canActivate: [AuthGuardService]
  },
  {
    path: 'logout',
    redirectTo: 'admin/account/logout',
    pathMatch: 'full',
    canActivate: [AuthGuardService]
  },
  {
    path: 'account/password-forgotten',
    redirectTo: 'admin/account/password-forgotten',
    pathMatch: 'full',
    canActivate: [AuthGuardService]
  },
  {
    path: 'reset-password/:key',
    redirectTo: 'admin/account/password-reset/:key',
    pathMatch: 'full',
    canActivate: [AuthGuardService]
  },
  {
    path: 'admin',
    loadChildren: './modules/admin/admin.module#AdminModule',
    canActivate: [AuthGuardService]
  },
  {
    path: 'account',
    loadChildren: './modules/account/account.module#AccountModule',
    canActivate: [AuthGuardService]
  },
  {
    path: 'vr',
    loadChildren: './modules/start-vr/start-vr.module#StartVrModule',
    canActivate: [AuthGuardService]
  },
  {
    path: '**',
    redirectTo: 'vr/home'
  },
  {
    path: '',
    redirectTo: 'vr/home',
    pathMatch: 'full'
  }
];
