import {Inject, Injectable} from '@angular/core';
import {HttpCommonService} from './http-common.service';
import {IClient} from '../../interfaces/iClient';

@Injectable({
  providedIn: 'root'
})
export class CallCenterService  extends HttpCommonService {

  private basePath = '/api/call-center/clients';

  getAllClients(skipErrorInterceptor?: boolean) {
    return this.http.get<IClient[]>(this.getGatewayUrl(this.basePath), this.getOptions(skipErrorInterceptor));
  }
}
