import {Inject, Injectable} from '@angular/core';
import * as SockJS from 'sockjs-client';
import {Client, StompHeaders} from '@stomp/stompjs';
import {WEBSOCKETCHANNEL} from '../../enums/sockets/websocketchannel.enum';
import {StatusDetailService} from '../status-detail.service';
import {CALLSTATUS} from '../../enums/callstatus.enum';
import {WEBSOCKETAGENTSTATUS} from '../../enums/sockets/websocketagentstatus.enum';
import {WEBSOCKETCALLSTATUS} from '../../enums/sockets/websocketcallstatus.enum';
import {INotification, NgxNotificationsService} from 'ngx-notify';
import {Router} from '@angular/router';
import {IVisit} from '../../interfaces/iVisit';
import {IVisitor} from '../../interfaces/iVisitor';
import {IRequestToCallMsg} from '../../interfaces/webSocketMessages/iRequestToCallMsg';
import {ICallStatusMsg} from '../../interfaces/webSocketMessages/iCallStatusMsg';
import {IWSErrorMsg} from '../../interfaces/webSocketMessages/iWSErrorMsg';
import {CallcenterClientStatusService} from '../callcenter-client-status.service';
import {HttpProfileService} from 'ngx-uaa';

@Injectable({
  providedIn: 'root'
})
export class VrWebSocketService {

  private client: Client;
  private agentID: string;

  private callStatusSubscription;
  private callAgentsSubscription;
  private callErrorSubscription;
  private requestToCallSubscription;

  private lastRequestToCall: IRequestToCallMsg;
  private timer;

  constructor(
    private statusDetailService: StatusDetailService,
    private ngxNotificationsService: NgxNotificationsService,
    private callcenterClientStatusService: CallcenterClientStatusService,
    private httpProfileService: HttpProfileService,
    private router: Router,
    @Inject('WEBSOCKETURL') private serverUrl,
    @Inject('WEBSOCKETDEBUG') private webSocketDebug) {
  }

  initializeWebSocketConnection(agentID?: string) {
    this.agentID = agentID ? agentID : this.agentID;
    if (!this.client && this.agentID) {
      const headers: StompHeaders = {
        'client-type': 'agent'
      };
      this.client = new Client({
        webSocketFactory: () => {
          return new SockJS(this.serverUrl, {}, {transports: ['websocket']});
        },
        connectHeaders: headers,
        debug: (str) => {
          if (this.webSocketDebug && this.webSocketDebug === true) {
            console.log(str);
          }
        },
      });
      this.client.onConnect = (frame) => {
        this.requestToCallSubscription = this.client.subscribe(WEBSOCKETCHANNEL.REQUEST_TO_CALL, (str) => {
          this.lastRequestToCall = JSON.parse(str.body) as IRequestToCallMsg;
          // NB se sono in Pausa o Busy non arriva proprio
          this.sendAgentStatus(this.agentID, WEBSOCKETAGENTSTATUS.BUSY); // mi setto a occupato

          const agentStatusAtIncomingCallTime = this.callcenterClientStatusService.getCallcenterClientsStatusBS().getValue().get(this.agentID).status;
          if (agentStatusAtIncomingCallTime === WEBSOCKETAGENTSTATUS.FREE) {  // verifico che sia realmente free
            this.httpProfileService.current().subscribe(  // se non è scaduta la sessione, altrimenti non puoi creare la visita ecc
              () => {
                this.sendCallStatus(WEBSOCKETCALLSTATUS.RINGING);

                this.timer = setTimeout(() => {
                  this.statusDetailService.setStatus(CALLSTATUS.FREE);
                  this.sendAgentStatus(this.agentID, WEBSOCKETAGENTSTATUS.FREE);
                  this.lastRequestToCall = null;
                }, this.lastRequestToCall.ringingTimeout * 1000);
              }, () => {
                this.deactivateWebSocketConnection();
              }
            );
          }
        });

        this.callStatusSubscription = this.client.subscribe(WEBSOCKETCHANNEL.CALL_STATUS_OUT, (str) => {
          const callStatusReceived: ICallStatusMsg = JSON.parse(str.body) as ICallStatusMsg;
          if (this.lastRequestToCall && (this.lastRequestToCall.id === callStatusReceived.id)) {
            switch (callStatusReceived.status) {
              case WEBSOCKETCALLSTATUS.ANSWERED: {
                this.statusDetailService.setStatus(CALLSTATUS.CALL);
                break;
              }
              case WEBSOCKETCALLSTATUS.HANGUP: {
                this.statusDetailService.setStatus(CALLSTATUS.HANGUP);
                setTimeout(() => {
                  this.statusDetailService.setStatus(CALLSTATUS.FREE);
                  this.sendAgentStatus(this.agentID, WEBSOCKETAGENTSTATUS.FREE);
                  this.lastRequestToCall = null;
                }, 2000);
                break;
              }
              case WEBSOCKETCALLSTATUS.RINGING: {
                this.statusDetailService.setStatus(CALLSTATUS.RINGING, this.lastRequestToCall.recipient, this.lastRequestToCall.caller);
                break;
              }
              case WEBSOCKETCALLSTATUS.TIMEOUT: {
                this.statusDetailService.setStatus(CALLSTATUS.FREE);
                this.sendAgentStatus(this.agentID, WEBSOCKETAGENTSTATUS.FREE);
                this.lastRequestToCall = null;
                break;
              }
            }
          }
        });

        this.callAgentsSubscription = this.client.subscribe(WEBSOCKETCHANNEL.AGENTS_STATUS_OUT, (str) => {
          /*          const agent: { 'agent': string; 'status': string; 'message': string; } = JSON.parse(str.body);
                    const client: IClient = {
                      username: agent.agent,
                      status: agent.status,
                      message: agent.message,
                    };
                    this.callcenterClientStatusService.setCallCenterClientStatus(client);*/
          this.callcenterClientStatusService.refreshCallcenterClientsStatus();
        });

        this.callErrorSubscription = this.client.subscribe(WEBSOCKETCHANNEL.ERROR, (str) => {
          const errorObj: IWSErrorMsg = JSON.parse(str.body) as IWSErrorMsg;
          const notification: INotification = {body: errorObj.description || 'vr.common.error'};
          this.ngxNotificationsService.error(notification);
          if (errorObj.message === 'duplicate.connection') {
            this.router.navigate(['/logout']);
          }
        });
        this.sendAgentStatus(this.agentID, WEBSOCKETAGENTSTATUS.FREE);
      };
      this.client.activate();
    }
  }

  deactivateWebSocketConnection() {
    if (this.client) {
      this.client.deactivate();
      this.client = null;
      this.agentID = null;
    } else {
    }
  }

  sendCallStatus(webSocketCallStatus: string) {
    if (this.lastRequestToCall) {
      clearTimeout(this.timer);
      const msg = {
        id: this.lastRequestToCall.id,
        status: webSocketCallStatus
      };
      this.client.publish({
        destination: '/app/' + WEBSOCKETCHANNEL.CALL_STATUS_IN,
        body: JSON.stringify(msg)
      });
    }
  }

  sendAgentStatus(agentId: string, webSocketAgentStatus: string) {
    const msg = {
      agent: agentId,
      status: webSocketAgentStatus
    };
    this.client.publish({
      destination: '/app/' + WEBSOCKETCHANNEL.AGENTS_STATUS_IN,
      body: JSON.stringify(msg)
    });
    this.callcenterClientStatusService.refreshCallcenterClientsStatus();
  }

  sendTicketRequest(visit: IVisit) {
    const channel = WEBSOCKETCHANNEL.TICKET_REQUEST_IN + visit.callRequest.caller.id;
    visit.visitors.forEach((visitor: IVisitor) => {
      const msg = {
        accessCode: visitor.solariResponse,
        expireOn: visit.endDate,
        fullname: visitor.name + ' ' + visitor.surname
      };

      if (!msg.accessCode) {
        const notification: INotification = {body: 'vr.visit.action.confirm.error.accessCodeNull'};
        this.ngxNotificationsService.error(notification);
      } else {
        this.client.publish({
          destination: channel,
          body: JSON.stringify(msg)
        });
      }
    });
  }

  getLastCallRequest() {
    return this.lastRequestToCall;
  }

  getLastCallRequestID() {
    return Number(this.lastRequestToCall.id);
  }
}
