import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';

import {AppComponent} from './app.component';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {environment} from '../environments/environment';
import {HttpClientModule} from '@angular/common/http';
import {NgxNotifyLibModule} from 'ngx-notify';
import {PASSWORD_PATTERN, UAA_BACKGROUND_URL, UAA_BASE_HREF, UAA_LOGO_URL, UAA_RECAPTCHA_KEY} from 'ngx-uaa';
import {RouterModule} from '@angular/router';
import {APP_ROUTES} from './app-routing';
import {LoadingBarHttpClientModule} from '@ngx-loading-bar/http-client';
import { BankBranchesService } from './modules/start-vr/services/http/bank-branches.service';

@NgModule({
  declarations: [
    AppComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    LoadingBarHttpClientModule,
    HttpClientModule,
    NgxNotifyLibModule,

    RouterModule.forRoot(APP_ROUTES, {useHash: environment.useHash}),
  ],
  exports: [
    RouterModule
  ],
  providers: [
    {provide: UAA_LOGO_URL, useValue: environment.uaaLogoUrl},
    {provide: UAA_BASE_HREF, useValue: environment.uaaBaseUrl},
    {provide: UAA_RECAPTCHA_KEY, useValue: environment.uaaRecaptchaKey},
    {provide: UAA_BACKGROUND_URL, useValue: environment.uaaBackgroundUrl},
    {
      provide: PASSWORD_PATTERN,
      useValue: {pattern: new RegExp('^(?=.*[A-Za-z])(?=.*[0-9])(?=.*[!@#\$%\^&\*])(?=.{8,})')/*  */, helper: 'password_helper'}
    },
    {provide: 'JANUS_SERVER_LIST', useValue: environment.janusServerList},
    {provide: 'JANUS_SERVER_DEBUG', useValue: environment.janusDebug},
    {provide: 'WEBSOCKETURL', useValue: environment.webSocketUrl},
    {provide: 'WEBSOCKETDEBUG', useValue: environment.webSocketDebug},
    {provide: 'BASE_HREF', useValue: environment.baseHref},
    {provide: 'GATEWAY_HREF', useValue: environment.gatewayHref},
    {provide: 'i18n_ASSETS_PATH', useValue: environment.i18nAssetsPath},
    {provide: 'i18n_DEFAULT_LANG', useValue: environment.i18nDefaultLang}

  ],
  bootstrap: [AppComponent]
})
export class AppModule {
  constructor() {
    document.getElementById('mainspinner').remove();
  }
}
