import {Injectable} from '@angular/core';
import {BehaviorSubject} from 'rxjs';
import {ILogMessage} from '../interfaces/iLogMessage';
import {NgxNotificationsService} from 'ngx-notify';

@Injectable({
  providedIn: 'root'
})
export class LogService {

  private readonly MAX_HISTORY_LOG_SIZE: number = 100;

  private historyLog: ILogMessage [] = [];

  private logBS = new BehaviorSubject(null);

  constructor(private ngxNotificationsService: NgxNotificationsService) {
  }

  appendLog(level: string, msg: string, obj?: any) {
    const log = {
      level: level,
      date: new Date(),
      msg: msg,
      obj: obj ? obj : {}
    };
    if (this.historyLog.length >= this.MAX_HISTORY_LOG_SIZE) {
      this.historyLog.shift();
    }
    this.historyLog.push(log);
    this.logBS.next(log);

    // localStorage.setItem(new Date().getTime().toString(), JSON.stringify(log));
    // console.info(new Date().getTime().toString(), JSON.stringify(log));
    this.notify(log);
  }

  getLogBS() {
    this.logBS = new BehaviorSubject(this.historyLog);
    return this.logBS;
  }

  getHistoryLog() {
    return this.historyLog;
  }

  notify(log) {
    switch (log.level) {
      case 'INFO': {
        break;
      }
      case 'SUCCESS': {
        break;
      }
      case 'ERROR': {
        this.ngxNotificationsService.error({body: log.msg + JSON.stringify(log.obj)});
        break;
      }
    }
  }
}
